import { CButton, CCard, CCardBody } from '@coreui/react'
import {React} from 'react'
import { useHistory } from 'react-router-dom'

const Beranda = () =>{
    const history = useHistory()
    return(
        <CCard>
            <CCardBody>
                <div className="mb-3">
                    <b><h4>Menu : </h4></b>
                </div>
                <div className="d-flex flex-row">
                    <div className="mr-2">
                        <CButton onClick={()=>history.push('/dendrogram')} className="btn btn-success">Dendrogram</CButton>                        
                    </div>
                    {/* <div className="mr-2">
                        <CButton onClick={()=>history.push('/ocr')} className="btn btn-info">OCR</CButton>                        
                    </div>                     */}
                </div>
            </CCardBody>
        </CCard>
    )
}

export default Beranda